import type { FC } from 'react'
import AnimatedLink from '../AnimatedLink'

// Componente de menú móvil como Server Component
const MobileMenu: FC = () => {
    return (
        <div className="relative sm:hidden">
            <input
                id="mobile-menu-toggle"
                type="checkbox"
                className="peer hidden"
                title="Toggle mobile menu"
            />

            {/* Label que alterna ícono hamburguesa / cerrar */}
            <label
                htmlFor="mobile-menu-toggle"
                className="cursor-pointer text-primary focus:outline-none"
            >
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6 peer-checked:hidden"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                >
                    <path
                        fillRule="evenodd"
                        d="M3 6h18v2H3V6zm0 5h18v2H3v-2zm0 5h18v2H3v-2z"
                        clipRule="evenodd"
                    />
                </svg>
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6 hidden peer-checked:block"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                >
                    <path
                        fillRule="evenodd"
                        d="M5.293 6.707a1 1 0 011.414-1.414L12 10.586l5.293-5.293a1 1 0 111.414 1.414L13.414 12l5.293 5.293a1 1 0 01-1.414 1.414L12 13.414l-5.293 5.293a1 1 0 01-1.414-1.414L10.586 12 5.293 6.707z"
                        clipRule="evenodd"
                    />
                </svg>
            </label>

            <nav
                className="origin-top-right absolute right-0 mt-2 w-64 rounded-bl-2xl
            bg-gray-100 shadow-lg transform scale-y-0 opacity-0
            transition-transform duration-300
            peer-checked:scale-y-100 peer-checked:opacity-100"
            >
                <ul className="space-y-2 p-4 text-right">
                    <li><AnimatedLink href="/">Inicio</AnimatedLink></li>
                    <li><AnimatedLink href="/sobre-nosotros">Sobre Nosotros</AnimatedLink></li>
                    <li><AnimatedLink href="/derecho-penal">Derecho Penal</AnimatedLink></li>
                    <li><AnimatedLink href="/derecho-inmobiliario">Derecho Inmobiliario</AnimatedLink></li>
                    <li><AnimatedLink href="/derecho-familiar">Derecho Familiar</AnimatedLink></li>
                    <li><AnimatedLink href="/contacto">Contacto</AnimatedLink></li>
                    <li><AnimatedLink href="/preguntas-frecuentes">Preguntas Frecuentes</AnimatedLink></li>
                </ul>
            </nav>
        </div>
    )
}

export default MobileMenu

